<template>
    <div class="comment">
        <!-- <h3></h3> -->
        <van-cell>
            <template slot="title">
                <div class="mybox">
                    <div class="img">
                        <img :src="commentItem.aut_photo" alt="">
                    </div>
                    <div class="msg">
                        <div>{{ commentItem.aut_name }}</div>
                        <div>{{ commentItem.content }}</div>
                        <div>
                            <span>{{ commentItem.pubdate | datafilter }}</span>
                            <van-button v-if="iscomment" @click="openreply" size="mini" class="myreply" round type="danger"> {{ commentItem.reply_count }}</van-button>
                        </div>
                    </div>
                    <div class="follorw">
                        <van-icon name="like" /> {{ commentItem.like_count }}
                    </div>
                </div>
            </template>
        </van-cell>
    </div>
</template>

<script>
//  bus
import bus from '../utils/bus'
export default {
    // commentItem
    // iscomment:  true  false 
    props: ['commentItem', 'iscomment'],
    methods: {
        //  replay
        openreply() {
            //  reply 
            this.$emit('openreply', true)
            //  reply 
            bus.$emit('passitem', this.commentItem)
        }
    }
}
</script>

<style lang="less" scoped>
.mybox {
    display: flex;
    .img {
        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }
    .msg {
        flex: 1;
        span {
            margin: 0;
        }
    }
}

.myreply {
    margin-left: 10px;
}
</style>
