<template>
    <div>
        <!--  -->
        <van-nav-bar class="mynavbar" left-arrow @click-left="onClickLeft">
            <template slot="title">
                <div class="myword"></div>
            </template>
        </van-nav-bar>
        <!--  -->
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
            <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoad">
                <van-cell @click="toDetail(item.art_id)" v-for="(item, index) in searchList" :key="index">
                    <template slot="title">
                        <!--  -->
                        <h3>{{ item.title }}</h3>
                        <!--  -->
                        <van-grid v-if="item.cover.type > 0" :border="false" :column-num="3">
                            <van-grid-item v-for="(subitem, subindex) in item.cover.images" :key="subindex">
                                <van-image :src="subitem" />
                            </van-grid-item>
                        </van-grid>
                        <!--  -->
                        <div class="msg">
                            <span>{{ item.aut_name }}</span>
                            <span>{{ item.comm_count }} </span>
                            <span>{{ item.pubdate | datafilter }}</span>
                        </div>
                        <!--  -->
                        <van-grid :column-num="3">
                            <van-grid-item>
                                <template slot="text">
                                    <div class="mybxo">
                                        <van-icon @click="comment" name="comment-o" /> 
                                    </div>
                                </template>
                            </van-grid-item>
                            <van-grid-item>
                                <template slot="text">
                                    <div class="mybxo">
                                        <van-icon @click="zan" name="like-o" />
                                    </div>
                                </template>
                            </van-grid-item>
                            <van-grid-item>
                                <template slot="text">
                                    <div @click="share" class="mybxo">
                                        <van-icon name="share" /> 
                                    </div>
                                </template>
                            </van-grid-item>
                        </van-grid>
                    </template>
                </van-cell>
            </van-list>
        </van-pull-refresh>
    </div>
</template>

<script>
// 
import { apiGetSearchList } from '../../api/article'
export default {
    data() {
        return {
            loading: false,
            finished: false,
            isLoading: false,
            // 
            key: '',
            // 
            page: 0,
            // 
            perpage: 10,
            // 
            searchList: [],
            // 
            totalcount: 0
        }
    },
    methods: {
        // list 
        async onLoad() {
            this.page = this.page + 1
            // 
            let res = await apiGetSearchList({
                page: this.page,
                perpage: this.perpage,
                key: this.key
            })
            this.searchList = [...this.searchList, ...res.data.data.results]
            //  
            this.totalcount = res.data.data.total_count
            // 
            if (this.searchList.length === this.totalcount) {
                this.finished = true
            }
            //  false
            this.loading = false
        },
        // 
        onRefresh() {
            // 
            this.loading = false
            this.finished = false
            this.page = 0
            this.searchList = []
            this.totalcount = 0
            this.isLoading = false
            // 
            this.onLoad()
        },
        // 
        onClickLeft() {
            // 
            // console.log('')
            //  vue  vue-router 
            // vue-router  $router
            //       $router  back()
            this.$router.back()
        },
        // 
        comment() {
            // 
            // 
            let user = this.$store.state.user
            // 
            if (!user.token) {
                // 
                this.$router.push('/login')
                return
            }
            console.log('')
        },
        // 
        zan() {
            //   Vue 
            if (!this.$login()) {
                return
            }
            console.log('')
        },
        // 
        share() {
            if (!this.$login()) {
                return
            }
            console.log('')
        },
        // 
        toDetail(artid) {
            this.$router.push(`/detail/${artid}`)
        }
    },
    created() {
        this.key = this.$route.params.key
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

.mybxo {
    display: flex;
    align-items: center;
}

.msg {
    span {
        margin-right: 10px;
    }
}
</style>
