<template>
    <div class="detail">
        <!--  -->
        <van-nav-bar class="mynavbar" left-arrow @click-left="$router.back()">
            <template slot="title">
                <div class="myword">
                    {{ detailObj.title }}
                </div>
            </template>
        </van-nav-bar>
        <!--  -->
        <h1 class="title">
            {{ detailObj.title }}
        </h1>
        <!--  -->
        <van-cell>
            <template slot="title">
                <div class="box">
                    <div class="img">
                        <img :src="detailObj.aut_photo" alt="">
                    </div>
                    <div class="msg">
                        <div>{{ detailObj.aut_name }}</div>
                        <div>{{ detailObj.pubdate | datafilter }}</div>
                    </div>
                    <div class="follorw">
                        <!--   +  -->
                        <van-button @click="follow" v-if="detailObj.is_followed === false" type="info">+ </van-button>
                        <van-button @click="unfollow" v-if="detailObj.is_followed === true" type="info"></van-button>
                    </div>
                </div>
            </template>
        </van-cell>
        <!--  -->
        <van-cell>
            <template slot="title">
                <div v-html="detailObj.content"></div>
            </template>
        </van-cell>
        <!--  &  -->
        <div class="btnbox">
            <!-- attitude-1   0  1  -->
            <van-button @click="zan" class="mybtn" round plain :type="detailObj.attitude === 1 ? 'danger' : 'default'">
                <van-icon name="like-o" />
            </van-button>
            <van-button @click="dislike" class="mybtn" round plain :type="detailObj.attitude === 0 ? 'danger' : 'default'">
                <van-icon name="delete" />
            </van-button>
        </div>
        <!--  -->
        <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoad">
            <!-- item  -->
            <comment :iscomment="true" @openreply="openreply" :commentItem="item" v-for="(item, index) in commentList" :key="index" />
        </van-list>
        <!--   id -->
        <write :isComment="true" :artid="artid" @addComment="addComment" />
        <!--  -->
        <!--  id -->
        <reply :artid="artid" v-model="replyshow" />
    </div>
</template>

<script>
import comment from '../../components/comment'
import write from '../../components/write'
import reply from '../../components/reply'
// 
import { apiGetDetail, apiZan, apiDislike } from '../../api/article'
// 
import { apiFollowUse, apiUnFollowUse } from '../../api/user'
// 
import { apiGetComment } from '../../api/pinglun'

export default {
    data() {
        return {
            artid: 0, //  id
            detailObj: {}, //  attitude
            loading: false, // list 
            finished: false, // list 
            offset: null, // 
            limit: 10,
            commentList: [], // 
            endid: 0, // 
            replyshow: false
        }
    },
    methods: {
        //  id 
        async getDetail() {
            let res = await apiGetDetail(this.artid)
            this.detailObj = res.data.data
        },
        // 
        async follow() {
            // 
            let autid = this.detailObj.aut_id
            // 
            await apiFollowUse(autid)
            //  true
            this.detailObj.is_followed = true
        },
        // 
        async unfollow() {
            try {
                // 
                let autid = this.detailObj.aut_id
                await apiUnFollowUse(autid)
            } catch (error) {
                console.log(error)
            }
            //  false
            this.detailObj.is_followed = false
        },
        // 
        async zan() {
            // 
            let res = await apiZan(this.artid)
            // 
            this.detailObj.attitude = 1
        },
        // 
        async dislike() {
            await apiDislike(this.artid)
            this.detailObj.attitude = 0
        },
        // 
        async onLoad() {
            // 
            let res = await apiGetComment({
                artid: this.artid,
                offset: this.offset,
                limit: this.limit
            })
            // 
            this.commentList = [...this.commentList, ...res.data.data.results]
            this.endid = res.data.data.end_id
            // 
            this.offset = res.data.data.last_id
            // 
            if (this.offset === this.endid) {
                this.finished = true
            }
            //  List  false
            this.loading = false
        },
        addComment(commentObj) {
            // 
            // console.log(commentObj)
            //  commentList 
            this.commentList.unshift(commentObj)
        },
        // 
        openreply(bool) {
            //  replayshow
            this.replyshow = bool
        }
    },
    components: {
        comment,
        write,
        reply
    },
    created() {
        //  id
        this.artid = this.$route.params.artid
        //  id 
        this.getDetail()
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

.detail {
    margin-bottom: 54px;
}

.detail .title {
    text-align: center;
    font-weight: 400;
    margin: 15px;
}

.box {
    display: flex;
    align-items: center;
    .img {
        img {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            margin-right: 10px;
        }
    }
    .msg {
        flex: 1;
    }
}

.btnbox {
    text-align: center;
    .mybtn {
        margin: 20px;
    }
}
</style>
