// 
//  http
import http from "../utils/http";

//  id 
function apiGetChannelsArticel({ channelid, timestamp }) {
  return http({
    //  axios  baseUrl  baseUrl 
    url: "http://ttapi.research.itcast.cn/app/v1_1/articles",
    params: {
      channel_id: channelid,
      timestamp: timestamp,
      with_top: 0
    }
  });
}

// 
function apiDislike(artid) {
  return http({
    url: "/article/dislikes",
    method: "POST",
    data: {
      target: artid
    }
  });
}

// 
function apiReportArticle({ artid, type }) {
  return http({
    url: "/article/reports",
    method: "POST",
    data: {
      target: artid,
      type: type,
      remark: ""
    }
  });
}

// 
function apiGetSearchList({ page, perpage, key }) {
  return http({
    url: "/search",
    method: "GET",
    params: {
      page: page,
      per_page: perpage,
      q: key
    }
  });
}

// 
function apiGetDetail(artid) {
  return http({
    url: `/articles/${artid}`
  });
}

// 
function apiZan(artid) {
  return http({
    url: "/article/likings",
    method: "POST",
    data: {
      target: artid
    }
  });
}

// 
export {
  apiGetChannelsArticel,
  apiDislike,
  apiReportArticle,
  apiGetSearchList,
  apiGetDetail,
  apiZan
};
