<template>
    <div>
        <!--  -->
        <!-- v-model -->
        <!-- position  -->
        <van-popup :value="value" @input="$emit('input', $event)" position="bottom" :style="{ height: '90%' }">
            <van-cell>
                <template slot="title">
                    <van-icon name="cross" @click="$emit('input', false)" />
                </template>
            </van-cell>
            <!--  -->
            <van-cell title="">
                <template slot="default">
                    <van-button plain round type="danger" size="mini" v-if="showIcon===false" @click="showIcon=true"></van-button>
                    <van-button plain round type="danger" size="mini" v-if="showIcon===true" @click="showIcon=false"></van-button>
                </template>
            </van-cell>
            <!--  -->
            <van-grid>
                <van-grid-item class="fatheritem" v-for="(item, index) in channelList" :key="index">
                    <template slot="text">
                        <div @click="changeChannel(index)" :class="{active: index === activeChannels}">
                            {{ item.name }}
                            <van-icon @click.stop="delChannel(index)" v-if="showIcon && index != 0" name="clear" class="sonicon" />
                        </div>
                    </template>
                </van-grid-item>
            </van-grid>
            <!--  -->
            <van-cell title=""></van-cell>
            <!--  -->
            <van-grid>
                <van-grid-item @click="addChannel(item)" :text="item.name" v-for="(item, index) in otherChannels" :key="index" />
            </van-grid>
        </van-popup>
    </div>
</template>

<script>
// 
import { apiGetAllChannels, apiResetChannels } from '../api/channels'
//  local 
import { setLocal } from '../utils/mylocal'

export default {
    //     
    props: ['value', 'channelList', 'activeChannels'],
    data() {
        return {
            // 
            allChannels: [],
            // 
            showIcon: false
        }
    },
    methods: {
        // 
        async getAllChannels() {
            try {
                let res = await apiGetAllChannels()
                this.allChannels = res.data.data.channels
            } catch (error) {
                console.log('error')
            }
        },
        // 
        async addChannel(item) { // id name
            //  item 
            this.$set(item, 'loading', false)
            this.$set(item, 'finished', false)
            this.$set(item, 'articleList', [])
            this.$set(item, 'isLoading', false)
            this.$set(item, 'timestamp', null)
            //  item  channelList 
            this.channelList.push(item)
            // 
            let token = this.$store.state.user.token
            if (token) { // 
                //  channels 
                // 
                let channels = this.channelList.slice(1).map((item, index) => {
                    return {
                        id: item.id,
                        seq: index + 2
                    }
                })
                //  channels 
                let res = await apiResetChannels(channels)
                console.log(res)
            } else { // 
                // 
                setLocal('channels', this.channelList)
            }
        },
        // 
        async delChannel(index) {
            // index 
            // 
            this.channelList.splice(index, 1)
            let token = this.$store.state.user.token
            // 
            if (token) { // 
                //  channles
                let channels = this.channelList.slice(1).map((item, index) => {
                    return {
                        id: item.id,
                        seq: index + 2
                    }
                })
                // 
                await apiResetChannels(channels)
            } else { // 
                setLocal('channels', this.channelList)
            }
        },
        // 
        changeChannel(index) {
            // index: 
            //  home
            this.$emit('update:activeChannels', index)
        }
    },
    // 
    //     1
    //     2
    computed: {
        // 
        otherChannels: function() {
            //  allChannels  channelList 
            // 1 channelList  id 
            let idsArr = this.channelList.map(item => {
                return item.id
            })
            // 2)
            let resArr = this.allChannels.filter(item => {
                //  item.id  idsArr 
                return idsArr.indexOf(item.id) === -1
            })
            // 
            return resArr
        }
    },
    created() {
        // 
        this.getAllChannels()
    }
}
</script>

<style lang="less" scoped>
.fatheritem {
    position: relative;
    .sonicon {
        position: absolute;
        top: 0px;
        right: 0px;
        color: #ccc;
    }
}

.active {
    color: red;
}
</style>
