<template>
    <div class="userinfo">
        <van-nav-bar class="mynavbar" left-arrow @click-left="$router.back()" right-text="" @click-right="save">
            <template slot="title">
                <div class="myword">
                    
                </div>
            </template>
        </van-nav-bar>
        <van-cell-group>
            <van-cell title="" @click="showImgpop" is-link>
                <template slot="default">
                    <img class="myimg" :src="userInfo.photo" alt="">
                </template>
            </van-cell>
            <van-cell title="" @click="openName" is-link :value="userInfo.name" />
            <van-cell title="" is-link :value="userInfo.intro" />
        </van-cell-group>
        <van-cell-group>
            <van-cell title="" is-link :value="usermsg.gender === 0 ? '': ''" />
            <van-cell title="" is-link :value="usermsg.birthday" />
        </van-cell-group>
        <!--  name  -->
        <van-popup v-model="nameshow" position="bottom" :style="{ height: '7%' }">
            <van-field v-model="userInfo.name" required />
        </van-popup>
        <!--  -->
        <van-popup v-model="imgpopShow">
            <div class="box">
                <van-button class="mybtn" @click="showimgselect" size="large"></van-button>
            </div>
            <input type="file" @change="changeimg" style="display: none" ref="myfile">
            <div>
                <van-button class="mybtn" size="large"></van-button>
            </div>
            <div>
                <van-button class="mybtn" size="large"></van-button>
            </div>
        </van-popup>
    </div>
</template>

<script>
// 
import { apiGetUserInfo, apiGetUserProfile, apiSetUserProfile, apiSetUserImg } from '../../api/user'

import Vue from 'vue';
import { ImagePreview } from 'vant';

Vue.use(ImagePreview);

export default {
    data() {
        return {
            userInfo: {},
            usermsg: {},
            //  name 
            nameshow: false,
            //   
            imgpopShow: false
        }
    },
    methods: {
        // 
        async save() {
            try {
                await apiSetUserProfile({
                    name: this.userInfo.name,
                    intro: this.userInfo.intro,
                    gender: this.usermsg.gender,
                    birthday: this.usermsg.birthday
                })
                // 
                this.$toast.success('')
            } catch (error) {
                // 
                this.$toast.fail('')
            }
        },
        //  &  &        &  & 
        //            user           /user/profile
        async getUserInfo() {
            //  &  & 
            let res1 = await apiGetUserInfo()
            //   &  & 
            this.userInfo = res1.data.data
            //   & 
            let res2 = await apiGetUserProfile()
            //   & 
            this.usermsg = res2.data.data
        },
        //  name 
        openName() {
            this.nameshow = true
        },
        showImgpop() {
            this.imgpopShow = true
        },
        // 
        showimgselect() {
            // 
            let myfile = this.$refs.myfile
            //  
            myfile.click()
        },
        // 
        changeimg() {
            // 
            let myimg = this.$refs.myfile.files[0]
            // 
            //  
            // let imgpath = URL.createObjectURL(myimg) // 
            //   base64 
            //  fileReader  base64 
            //  fileReader 
            let reader = new FileReader()
            // 
            reader.readAsDataURL(myimg)
            //  load
            reader.addEventListener('load', () => {
                // console.log(reader.result)
                // 
                ImagePreview({
                    // 
                    images: [
                        reader.result
                    ],
                    // 
                    onClose: () => {
                        // 
                        this.setProfile()
                    }
                });
            })

        },
        // 
        setProfile() {
            // 
            this.imgpopShow = false
            // 
            this.$dialog.confirm({
                title: '',
                message: ''
            }).then(async () => {
                // 
                let myimg = this.$refs.myfile.files[0]
                // 
                let res = await apiSetUserImg(myimg)
                // 
                // console.log(res.data.data.photo)
                let imgPath = res.data.data.photo
                // 
                this.userInfo.photo = imgPath
            }).catch(() => {
                console.log('')
            });
        }
    },
    mounted() {
        // 
        this.getUserInfo()
    }
}
</script>

<style lang="less">
.mynavbar {
    background-color: #3296fa;
    .myword {
        color: #fff;
    }
}

i.van-icon.van-icon-arrow-left.van-nav-bar__arrow {
    color: #fff;
}

span.van-nav-bar__text {
    color: #fff;
}

.van-nav-bar__text:active {
    background-color: #3296fa;
    color: red
}

.userinfo {
    .myimg {
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }
}

.van-popup.van-popup--center {
    width: 80%;
}

.box .mybtn {
    display: block;
}
</style>
