<template>
    <div>
        <!--  -->
        <!-- v-model:  show-action -->
        <!-- search  -->
        <!-- cancel -->
        <van-search background="#3296fa" v-model="key" placeholder="" show-action @input="thinkFn" @search="onSearch">
            <template slot="action">
                <div class="cancleBtn" @click="onCancel">
                    
                </div>
            </template>
        </van-search>
        <!--  -->
        <van-cell-group v-if="thinkList.length > 0">
            <van-cell @click="onSearch(item.item)" v-for="(item, index) in thinkList" :key="index" icon="search">
                <template slot="title">
                    <div v-html="item.colorItem"></div>
                </template>
            </van-cell>
        </van-cell-group>
        <!--  -->
        <van-cell-group v-else>
            <van-cell title="">
                <template slot="right-icon">
                    <van-icon @click="delAll" name="delete" />
                </template>
            </van-cell>
            <van-cell @click="onSearch(item)" v-for="(item, index) in searchList" :key="index" icon="search" :title="item">
                <template slot="right-icon">
                    <van-icon @click.stop="delHis(index)" name="cross" />
                </template>
            </van-cell>
        </van-cell-group>
    </div>
</template>

<script>
// 
import { apiGetThinkList } from '../../api/common'
//  local 
import * as mylocal from '../../utils/mylocal'
export default {
    data() {
        return {
            key: '',
            // 
            thinkList: [],
            // 
            timer: null,
            // 
            searchList: mylocal.getLocal('history') || []
        }
    },
    methods: {
        // 
        onSearch(value) {
            // value
            //  value
            //  searchList 
            this.searchList.unshift(value.trim())

            // 
            this.searchList = [...new Set(this.searchList)]

            //  searchList 
            mylocal.setLocal('history', this.searchList)
            // 
            this.$router.push(`/searchResult/${value}`)
        },
        // 
        onCancel() {
            this.key = ""
            this.thinkList = []
        },
        // 
        thinkFn() {
            // 
            clearTimeout(this.timer)
            // 
            this.timer = setTimeout(async () => {
                // 
                if (this.key.trim().length === 0) {
                    // 
                    this.thinkList = []
                    // 
                    return
                }
                //  ==> this.key
                let res = await apiGetThinkList(this.key)
                this.thinkList = res.data.data.options
                // 
                this.thinkList = this.thinkList.map(item => {
                    /// ,,span
                    return {
                        // 
                        colorItem: item.split(this.key).join(`<span style="color: red">${this.key}</span>`),
                        // 
                        item: item
                    }
                })
            }, 300)
        },
        // 
        delHis(index) {
            // 
            this.searchList.splice(index, 1)
            // 
            mylocal.setLocal('history', this.searchList)
        },
        // 
        delAll() {
            // 
            this.$dialog.confirm({
                title: '',
                message: ''
            }).then(() => {
                // 
                this.searchList = []
                //  local
                mylocal.removeLocal('history')
            }).catch(() => {
            });
        }
    }
}
</script>

<style lang="less">
.cancleBtn {
    color: #fff;
}

.van-search__action:active {
    background-color: #3296fa;
    .cancleBtn {
        color: #ccc;
    }
}
</style>
