<template>
    <div class="write">
        <van-search v-model="value" placeholder="" show-action shape="round" @search="writeCommit">
            <div slot="action" @click="writeCommit"></div>
        </van-search>
    </div>
</template>

<script>
// 
import { apiAddComment, apiAddCommentReply } from '../api/pinglun'


export default {
    //    id       id
    props: ['artid', 'isComment', 'commid'],
    data() {
        return {
            value: ''
        }
    },
    methods: {
        async writeCommit() {
            // 
            if (this.isComment === true) {
                // 
                // 
                let res = await apiAddComment({
                    artid: this.artid, //  id
                    content: this.value
                })
                // 
                let commentObj = res.data.data.new_obj
                //  detail 
                this.$emit('addComment', commentObj)
                // 
                this.value = ''
            } else { // 
                // target id
                // console.log(this.commid.toString())
                // content:   
                // this.value
                // art_id id
                // console.log(this.artid)
                let res = await apiAddCommentReply({
                    commid: this.commid.toString(),
                    content: this.value,
                    artid: this.artid
                })
                // 
                let replyObj = res.data.data.new_obj
                //  reply 
                this.$emit('addComment', replyObj)
            }
        }
    }
}
</script>

<style lang="less">
.write {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    border-top: 1px solid #ccc;
}
</style>
