// 
import http from "../utils/http";

// 
function apiGetComment({ artid, offset, limit }) {
  // aioxs  null, aioxs 
  return http({
    url: "comments",
    params: {
      type: "a",
      source: artid, // id
      offset: offset,
      limit: limit
    }
  });
}

// 
function apiAddComment({ artid, content }) {
  return http({
    url: "/comments",
    method: "POST",
    data: {
      target: artid,
      content: content
    }
  });
}

// 
function apiGetCommentReply({ commid, offset, limit }) {
  return http({
    url: "/comments",
    method: "GET",
    params: {
      type: "c",
      source: commid, //  id
      offset: offset, // 
      limit: limit
    }
  });
}

// 
function apiAddCommentReply({ commid, content, artid }) {
  return http({
    url: "/comments",
    method: "POST",
    data: {
      target: commid,
      content: content,
      art_id: artid
    }
  });
}

export { apiGetComment, apiAddComment, apiGetCommentReply, apiAddCommentReply };
