// 
//  axios
import http from "../utils/http";

// 
function apiGetChannels() {
  return http({
    url: "/user/channels"
  });
}

// 
function apiGetAllChannels() {
  return http({
    url: "/channels"
  });
}

// 
function apiResetChannels(channels) {
  return http({
    url: "/user/channels",
    method: "PUT",
    data: {
      channels: channels
    }
  });
}

// 
export { apiGetChannels, apiGetAllChannels, apiResetChannels };
