// 
//  http
import http from "../utils/http";

// 
function apiLogin({ mobile, code }) {
  return http({
    url: "/authorizations",
    method: "POST",
    data: {
      mobile,
      code
    }
  });
}

// 
function apiBlackList(autid) {
  return http({
    url: "/user/blacklists",
    method: "POST",
    data: {
      target: autid
    }
  });
}

// 
function apiFollowUse(autid) {
  return http({
    url: "/user/followings",
    method: "POST",
    data: {
      target: autid
    }
  });
}

// 
function apiUnFollowUse(autid) {
  return http({
    url: `/user/followings/${autid}`,
    method: "DELETE"
  });
}

// 
function apiGetUserInfo() {
  return http({
    url: "/user"
  });
}

// 
function apiGetUserProfile() {
  return http({
    url: "/user/profile"
  });
}

// 
function apiSetUserProfile({ name, gender, birthday, intro }) {
  return http({
    url: "/user/profile",
    method: "PATCH",
    data: {
      name,
      gender,
      birthday,
      intro
    }
  });
}

// 
function apiSetUserImg(photo) {
  // axios 
  //  formData 
  //  fd 
  var fd = new FormData();
  // 
  fd.append("photo", photo);
  return http({
    url: "/user/photo",
    method: "PATCH",
    data: fd
  });
}

// 
export {
  apiLogin,
  apiBlackList,
  apiFollowUse,
  apiUnFollowUse,
  apiGetUserInfo,
  apiGetUserProfile,
  apiSetUserProfile,
  apiSetUserImg
};
