<template>
    <div>
        <!--  -->
        <van-dialog :value="value" :showConfirmButton="false">
            <!--  -->
            <van-cell-group v-if="isReport === false">
                <van-cell @click="$emit('input', false)">
                    <template slot="title">
                        <van-icon name="cross" />
                    </template>
                </van-cell>
                <van-cell icon="send-gift-o" @click="dislike" title="" />
                <van-cell icon="info-o" @click="report" title="" is-link />
                <van-cell icon="delete" @click="blackList" title="" />
            </van-cell-group>
            <!--  -->
            <van-cell-group v-if="isReport === true">
                <van-cell icon="arrow-left" @click="isReport = false"></van-cell>
                <van-cell @click="reportArticle(item.id)" v-for="(item, index) in reportList" :key="index" :title="item.name" />
            </van-cell-group>
        </van-dialog>
    </div>
</template>

<script>
// 
import { apiDislike, apiReportArticle } from '../api/article'
// 
import { apiBlackList } from '../api/user'

export default {
    // & id  id
    props: ['value', 'artid', 'autid'],
    data() {
        return {
            // 
            reportList: [
                { id: 0, name: '' },
                { id: 1, name: '' },
                { id: 2, name: '' },
                { id: 3, name: '' },
                { id: 4, name: '' },
                { id: 5, name: '' },
                { id: 6, name: '' },
                { id: 7, name: '' },
                { id: 8, name: '' }
            ],
            // 
            isReport: false
        }
    },
    methods: {
        // 
        async dislike() {
            //  id  home
            this.$emit('dislike', this.artid)
            try {
                // 
                await apiDislike(this.artid)
            } catch (error) {
                console.log(error.message)
            }
            // 
            this.$emit('input', false)
        },
        // 
        report() {
            // 
            this.isReport = true
        },
        // 
        async reportArticle(type) {
            // 
            try {
                await apiReportArticle({
                    artid: this.artid,
                    type: type
                })
            } catch (error) {
                console.log(error.message)
            }
            // 
            this.isReport = false
            this.$emit('input', false)
            // 
            this.$toast('3')
        },
        // 
        async blackList() {
            //  id
            // this.autid
            try {
                await apiBlackList(this.autid)
            } catch (error) {
                console.log(error.message)
            }
            // 
            this.$emit('input', false)
        }
    }
}
</script>

