//  Vue
import Vue from "vue";
//  Vuex
import Vuex from "vuex";
import { setLocal, getLocal } from "../utils/mylocal";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // 
    user: getLocal("userinfo") || {}
  },
  mutations: {
    setUser: function(state, payload) {
      //  user 
      state.user = payload;
      //  localstoreage 
      setLocal("userinfo", payload);
    }
  }
});
