<template>
    <div class="login">
        <!-- navBar -->
        <van-nav-bar class="mynavbar">
            <template slot="title">
                <div class="myword"></div>
            </template>
        </van-nav-bar>
        <!--  -->
        <van-cell-group>
            <van-field v-model="user.mobile" clearable placeholder="" :error-message="yanzheng.mobile">
                <template slot="left-icon">
                    <i class="iconfont icon-iphone"></i>
                </template>
            </van-field>
            <van-field left-icon="contact" v-model="user.code" placeholder="" :error-message="yanzheng.code">
                <template slot="left-icon">
                    <i class="iconfont icon-lock"></i>
                </template>
                <van-button class="mycodebtn" slot="button" size="small"></van-button>
            </van-field>
        </van-cell-group>
        <!--  -->
        <div class="myloginbtn">
            <van-button :loading="isLoading" @click="login" class="mybtn" size="large"></van-button>
        </div>
    </div>
</template>

<script>
//  api
import { apiLogin } from '../../api/user'
export default {
    data() {
        return {
            user: {
                mobile: '13911111111',
                code: '246810'
            },
            yanzheng: {
                // 
                mobile: "",
                code: ""
            },
            // 
            isLoading: false
        }
    },
    methods: {
        // 
        login() {
            if (!this.checking()) {
                return
            }
            //  js  try-catch
            //   try 
            //   catch 
            this.isLoading = true
            setTimeout(async () => {
                try {
                    // 
                    let res = await apiLogin(this.user)
                    //  vuex & localstoreage 
                    this.$store.commit('setUser', res.data.data)
                    console.log(this.$store.state.user)
                    // 
                    let path = this.$route.path
                    //  login  home
                    if (path === "/login") {
                        this.$router.push('/home')
                    }
                    //  checklogin 
                    if (path === "/checklogin") {
                        this.$router.back()
                    }
                } catch (error) {
                    this.$toast.fail('');
                }
                this.isLoading = false
            }, 1000);
        },
        checking() {
            //  & 
            let mobileF = true
            let codeF = true
            // 
            // 
            if (this.user.mobile.trim().length !== 11) {
                this.yanzheng.mobile = ""
                mobileF = false
            } else {
                this.yanzheng.mobile = ""
            }
            // 
            if (this.user.code.trim().length !== 6) {
                this.yanzheng.code = ""
                codeF = false
            } else {
                this.yanzheng.code = ""
            }
            // 
            return mobileF && codeF
        }
    }
}
</script>

<style lang="less" scoped>
.login {
    .mynavbar {
        background-color: #3296fa;
        .myword {
            color: #fff;
        }
    }
    .mycodebtn {
        background-color: #ececec;
        border-radius: 20px;
    }
    .myloginbtn {
        margin: 40px 20px;
        .mybtn {
            background-color: #6db4fb;
            color: #fff;
            border-radius: 5px;
        }
    }
}
</style>
