//  axios
import axios from "axios";
//  store
import store from "../store";
//  json-bigint
import jsonbigint from "json-bigint";

//  axios 
// axios.create
//     axios 
//      
//          
//               http://ttapi.research.itcast.cn/app/v1_0
//                  instance1
//               http://mm.research.itcast.cn/app/v1_0
//                  instance2
// 
let instance = axios.create({
  baseURL: "http://ttapi.research.itcast.cn/app/v1_0",
  //  axios  json
  transformResponse: [
    function(data) {
      try {
        // data  axios  json 
        // console.log(data);
        // 
        return jsonbigint.parse(data);
      } catch (error) {
        // 
        return JSON.parse(data);
      }
    }
  ]
});

//  axios  token
let tokenInstance = axios.create({
  baseURL: "http://ttapi.research.itcast.cn/app/v1_0"
});

// 
instance.interceptors.request.use(
  function(config) {
    // 
    let token = store.state.user.token;
    // let user = store.state.user;
    //  token 
    if (token) {
      //  token
      config.headers.authorization = `Bearer ${token}`;
    }
    return config;
  },
  function(error) {
    return Promise.reject(error);
  }
);

// 
instance.interceptors.response.use(
  function(response) {
    return response;
  },
  async function(error) {
    console.log("-------------------------------------");
    //  401
    //  401
    if (error.response.status === 401) {
      //  token 
      // 
      //  urlauthorizations
      //  methodPUT
      //  headers --> Authorization ---> refresh_token
      //  refresh_token
      let user = store.state.user;
      //  refreseh_token 
      if (user && user.refresh_token) {
        //  axios  token
        //  token: 
        let res = await tokenInstance({
          url: "/authorizations",
          method: "PUT",
          headers: {
            Authorization: `Bearer ${user.refresh_token}`
          }
        });
        //  token  store 
        user.token = res.data.data.token; // token refresh_token
        // console.log(user)
        // //  token
        store.commit("setUser", user);
        //  token 
        // 
        return instance(error.config) //  instance 
      }
    }
    console.log("-------------------------------------");
    return Promise.reject(error);
  }
);

//  axios 
export default instance;
